@import 'normalize.css';

:root {
    --white-color: #ffff;
    --black-color: #000;
    --primary-color: #b83f45;
    --text-color: #111;
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    font-size: 62.5%;
}
body {
    position: relative;
    font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 1.6rem;
    line-height: 1.4em;
    background: #f5f5f5;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    flex-direction: column;
    min-height: 100vh;
    width: 100%;
}

a {
    text-decoration: none;
}

.App {
    max-width: 550px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

// Reponsive

// Mobile and Tablet: width < 1024px
@media only screen and (max-width: 63.9375em) {
}

// Mobile: width < 740px
@media only screen and (max-width: 46.1875em) {
    body {
        font-size: 1.8rem;
    }
    .App {
        width: 99%;
    }
    .TodoItem_todo-item__HyKTN {
        min-width: 50%;
    }
    .TodoItem_edit__J7eKj,
    .TodoItem_delete__Qo\+3g {
        font-size: 2rem;
    }
    .TodoItem_edit-input__MR\+RZ {
        max-width: 90%;
    }
    .Footer_footer__kHKfE {
        padding: 4px 10px;
    }
    .Footer_filter__wWteJ {
        position: absolute;
        left: 24%;
        transform: none;
    }
    .Footer_selection-link__P8EuY {
        padding: 2px 6px;
        margin: 0;
    }
    .Footer_footer__kHKfE,
    .Footer_clear-completed__-6Jqa {
        font-size: 1.6rem;
    }
}

// Tablet: width >= 740px and width < 1024px
@media only screen and (min-width: 46.25em) and (max-width: 63.9375em) {
    body {
        font-size: 2.2rem;
    }
    .App {
        min-width: 85%;
    }
    .Header_input-wrapper__dJmC1 {
        width: 100%;
    }
    .TodoItem_todo-item__HyKTN {
        max-width: 56%;
    }
    .TodoItem_edit__J7eKj,
    .TodoItem_delete__Qo\+3g {
        font-size: 2.4rem;
    }
    .TodoItem_edit__J7eKj {
        margin-right: 30px;
    }
    .Footer_footer__kHKfE,
    .Footer_clear-completed__-6Jqa {
        font-size: 2.2rem;
    }
}

.grid {
    width: 100%;
    display: block;
    padding: 0;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px;
}

.row.no-gutters {
    margin-left: 0;
    margin-right: 0;
}

.col {
    padding-left: 4px;
    padding-right: 4px;
}

.row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
}

